import React, { useState, useEffect, useRef, useCallback } from "react";
import ForceGraph2D from "react-force-graph-2d";

const createReference = (name, version) => {
  return `${name}${version}`.replace(/\s+/g, "");
};

const iconMapping = {
  Mainframe: "Buildings.svg",
  Distributed: "Stack.svg",
  "Cloud-Based": "Wrench.svg",
  "Client-Server": "UsersThree.svg",
  "Stand-Alone": "ListMagnifyingGlass.svg",
  // Add paths to your icons as needed
};

const generateNewNodesAndLinks = (centerNodeId, initialData) => {
  const newNodes = [];
  const newLinks = [];

  const nodeMap = new Map(initialData.nodes.map((node) => [node.id, node]));

  const connectedLinks = initialData.links.filter(
    (link) =>
      (link.source === centerNodeId || link.target === centerNodeId) &&
      nodeMap.has(link.source) &&
      nodeMap.has(link.target)
  );

  const connectedNodeIds = connectedLinks.flatMap((link) => [
    link.source,
    link.target,
  ]);

  const connectedNodes = initialData.nodes.filter((node) =>
    connectedNodeIds.includes(node.id)
  );

  connectedNodes.forEach((node) => {
    if (!newNodes.some((n) => n.id === node.id)) {
      newNodes.push(node);
    }
  });

  connectedLinks.forEach((link) => {
    if (
      !newLinks.some(
        (l) => l.source === link.source && l.target === link.target
      )
    ) {
      newLinks.push(link);
    }
  });

  return { newNodes, newLinks };
};

const SingleGraph = ({ nodesData, linksData }) => {
  const initialData = {
    nodes: nodesData.map((node) => ({
      id: createReference(node.Name, node.Version),
      name: node.Name,
      group: 1,
      icon: iconMapping[node["Architecture Type"]] || "vite.svg",
    })),
    links: linksData.map((link) => ({
      source: createReference(link["Source Application"], ""),
      target: createReference(link["Target Application"], ""),
      label: "Sample Text",
    })),
  };
  const [data, setData] = useState({
    nodes: initialData.nodes.filter(
      (node) => node.id === createReference("CRM CSS", "3.2")
    ),
    links: [],
  });

  const graphRef = useRef();

  useEffect(() => {
    if (graphRef.current) {
      graphRef.current.d3Force("link").distance(150);
      graphRef.current.d3Force("charge").strength(-500);
    }
  }, []);

  const handleNodeClick = useCallback((node) => {
    const { newNodes, newLinks } = generateNewNodesAndLinks(
      node.id,
      initialData
    );

    if (newNodes.length > 0 || newLinks.length > 0) {
      setData((prevData) => {
        const updatedNodes = [
          ...prevData.nodes,
          ...newNodes.filter(
            (n) => !prevData.nodes.some((existing) => existing.id === n.id)
          ),
        ];
        const updatedLinks = [
          ...prevData.links,
          ...newLinks.filter(
            (l) =>
              !prevData.links.some(
                (existing) =>
                  existing.source === l.source && existing.target === l.target
              )
          ),
        ];

        return {
          nodes: updatedNodes,
          links: updatedLinks,
        };
      });

      setTimeout(() => {
        const { x, y } = node;
        graphRef.current.centerAt(x, y, 300);
      }, 100);
    }
  }, []);

  return (
    <div style={{ height: "100vh", width: "100vw" }}>
      <ForceGraph2D
        ref={graphRef}
        graphData={data}
        nodeRelSize={15}
        onNodeClick={handleNodeClick}
        linkWidth={2}
        linkDirectionalArrowLength={10}
        linkDirectionalArrowRelPos={1}
        linkColor={(link) => link.color || "black"}
        nodeCanvasObject={(node, ctx, globalScale) => {
          const size = 10 * globalScale;
          const fontSize = 8;

          ctx.fillStyle = node.color || "#ece0e6";
          ctx.beginPath();
          ctx.arc(node.x, node.y, size / 2, 0, 2 * Math.PI);
          ctx.closePath();
          ctx.fill();

          const icon = new Image();
          icon.src = node.icon;
          ctx.drawImage(
            icon,
            node.x - size / 4,
            node.y - size / 4,
            size / 2,
            size / 2
          );

          ctx.font = `${fontSize}px Sans-Serif`;
          ctx.fillStyle = "black";
          ctx.textAlign = "center";
          ctx.textBaseline = "middle";
          ctx.fillText(node.name, node.x, node.y + size);
        }}
        nodeCanvasObjectMode={() => "replace"}
        linkCanvasObjectMode={() => "after"}
        linkCanvasObject={(link, ctx, globalScale) => {
          const MAX_FONT_SIZE = 4; // Decreased font size
          const LABEL_NODE_MARGIN = 5;
          const LABEL_OFFSET = 5; // Reduced offset to bring text closer to the links

          const start = link.source;
          const end = link.target;

          if (typeof start !== "object" || typeof end !== "object") return;

          const relLink = { x: end.x - start.x, y: end.y - start.y };
          const linkLength = Math.sqrt(relLink.x ** 2 + relLink.y ** 2);

          // Calculate label positioning slightly offset from the link
          const labelPos = Object.assign(
            ...["x", "y"].map((c) => ({
              [c]:
                start[c] +
                (end[c] - start[c]) / 2 +
                (LABEL_OFFSET / linkLength) *
                  (c === "x" ? relLink.y : -relLink.x),
            }))
          );

          let textAngle = Math.atan2(relLink.y, relLink.x);
          if (textAngle > Math.PI / 2) textAngle = -(Math.PI - textAngle);
          if (textAngle < -Math.PI / 2) textAngle = -(-Math.PI - textAngle);

          const label = `${link.source.id} => ${link.target.id}`;

          ctx.font = "1px Sans-Serif";
          const fontSize = Math.min(
            MAX_FONT_SIZE,
            linkLength / ctx.measureText(label).width
          );
          ctx.font = `${fontSize}px Sans-Serif`;
          const textWidth = ctx.measureText(label).width;
          const bckgDimensions = [textWidth, fontSize].map(
            (n) => n + fontSize * 0.2
          );

          ctx.save();
          ctx.translate(labelPos.x, labelPos.y);
          ctx.rotate(textAngle);

          ctx.fillStyle = "rgba(255, 255, 255, 0.8)";
          ctx.fillRect(
            -bckgDimensions[0] / 2,
            -bckgDimensions[1] / 2,
            ...bckgDimensions
          );

          ctx.textAlign = "center";
          ctx.textBaseline = "middle";
          ctx.fillStyle = "darkgrey";
          ctx.fillText(label, 0, 0);
          ctx.restore();
        }}
      />
    </div>
  );
};

export default SingleGraph;
